import '../style/main.css';
import screen_img from '../resources/screen_img.webp'

function TopScreen(props) {
    // TODO: 이미지 이름을 props에서 받아와 사용하도록
    const {title, sub_title} = {props};
    return (
        <div class="top-screen">
            <img class="top-screen-image" src={screen_img} alt='Logo'></img>
            <div class="top-screen-title">
                {title}
            </div>
            <div class="top-screen-sub">
                {sub_title}
            </div>
        </div>
    )
}

export default TopScreen;